import React, { useEffect, useState } from "react";
import { getBase64ImageReducer } from "../reducers/commonReducer";
import { useDispatch } from "react-redux";
import parse from "html-react-parser";

const FileComponent = (props) => {
  const [fileBase64Format, setFileBase64Format] = useState("");
  const fileType = props.fileSrc.split(".").splice(-1)[0];
  const fileAlt = parse(props.alt);
  const dispatch = useDispatch();
  const getBase64Image = async (fileSrc) => {
    const fileName = fileSrc;
    if (fileName !== "" && fileName !== null) {
      const data = await dispatch(getBase64ImageReducer({ fileName }));
      if (data.payload && data.payload.fileBase64) {
        ["jpg", "jpeg", "png", "gif"].includes(fileType)
          ? setFileBase64Format(
              `data:image/jpeg;base64,${data.payload.fileBase64}`
            )
          : setFileBase64Format(
              `data:application/${fileType};base64,${data.payload.fileBase64}`
            );
      } else {
        if (["jpg", "jpeg", "png", "gif"].includes(fileType)) {
          setFileBase64Format(`/uploads/${fileSrc}`);
        } else {
          setFileBase64Format(`/uploadedFiles/${fileSrc}`);
        }
      }
    }
  };

  useEffect(() => {
    //setFileBase64Format("");
    getBase64Image(props.fileSrc);
  }, [props]);
  return (
    <>
      {["jpg", "jpeg", "png", "gif"].includes(fileType) === true ? (
        fileBase64Format === "" ? (
          <img
            src="/images/loading.gif"
            alt={fileAlt && fileAlt.props ? fileAlt.props.children : ""}
            style={props.fileStyle ? props.fileStyle : {}}
            className={props.fileClass ? props.fileClass : ""}
          />
        ) : (
          <img
            src={fileBase64Format}
            alt={fileAlt && fileAlt.props ? fileAlt.props.children : ""}
            style={props.fileStyle ? props.fileStyle : {}}
            className={props.fileClass ? props.fileClass : ""}
          />
        )
      ) : (
        <a
          href={fileBase64Format}
          title={fileAlt && fileAlt.props ? fileAlt.props.children : ""}
          style={props.fileStyle ? props.fileStyle : {}}
          className={props.fileClass ? props.fileClass : ""}
          download={props.fileSrc}
        >
          Download Attachment
        </a>
      )}
    </>
  );
};

export default FileComponent;
