import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { NavLink as ReactLink, useNavigate } from "react-router-dom";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { logoutUserReducer } from "../reducers/authReducer";
import FileComponent from "./FileComponent";

const NavBarComponent = (args) => {
  const navigate = useNavigate();
  const { userdetails } = useSelector((store) => store.user);
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  const logoutHandler = async () => {
    await dispatch(logoutUserReducer());
    navigate("/login");
  };
  return (
    <>
      <Navbar
        {...args}
        style={{ position: "sticky", top: "0px" }}
        className="pe-md-3"
      >
        <NavbarBrand tag={ReactLink} to="/">
          <img src="/images/codefolio.png" alt="Codefolio" title="Codefolio" />
        </NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ms-auto" navbar>
            <NavItem>
              <NavLink tag={ReactLink} to="/home">
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/portfolios">
                Portfolio
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/blog">
                Blog
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/components">
                Components
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={ReactLink} to="/contact">
                Contact
              </NavLink>
            </NavItem>
            <NavItem>
              {userdetails.token ? (
                <ul className="px-0">
                  <UncontrolledDropdown nav inNavbar>
                    <DropdownToggle nav>
                      {/* <img
                        src="/images/ux-design.jpg"
                        alt="UI/UX Design"
                        style={{ height: "25px", width: "25px" }}
                        className="d-none d-md-block"
                      /> */}
                      {userdetails.pic ? (
                        <FileComponent
                          fileSrc={userdetails.pic}
                          fileStyle={{ height: "25px", width: "25px" }}
                          fileClass="d-none d-sm-block"
                          alt={userdetails.name}
                        />
                      ) : (
                        <img
                          src="/images/user-avatar.jpg"
                          alt={userdetails.name}
                          style={{ height: "25px", width: "25px" }}
                          className="d-none d-sm-block"
                        />
                      )}
                      <span className="d-block d-sm-none">
                        {userdetails.email}
                      </span>
                    </DropdownToggle>
                    <DropdownMenu end className="mt-md-2">
                      <DropdownItem>
                        <div onClick={(e) => navigate("/profile")}>
                          My Profile
                        </div>
                      </DropdownItem>
                      {/* <DropdownItem>Option 2</DropdownItem> */}
                      <DropdownItem divider />
                      <DropdownItem>
                        <div onClick={logoutHandler}>Logout</div>
                      </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                </ul>
              ) : (
                <NavLink tag={ReactLink} to="/login">
                  Login
                </NavLink>
              )}
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </>
  );
};

export default NavBarComponent;
