import React from "react";
import { useDispatch } from "react-redux";
import { UncontrolledAlert } from "reactstrap";
import { clearError, clearSuccess } from "../reducers/commonReducer";
//import { clearError, clearSuccess } from "../reducers/authReducer";

const ErrorAlert = (props) => {
  const dispatch = useDispatch();
  const onDismiss = async (error) => {
    await dispatch(clearError(error));
  };

  return (
    <UncontrolledAlert color="danger" onClick={() => onDismiss(props.message)}>
      {props.message}
    </UncontrolledAlert>
  );
};
const SuccessAlert = (props) => {
  const dispatch = useDispatch();
  const onDismiss = async () => {
    await dispatch(clearSuccess());
  };

  return (
    <UncontrolledAlert color="primary" onClick={onDismiss}>
      {props.message}
    </UncontrolledAlert>
  );
};

export { ErrorAlert, SuccessAlert };
