import React from "react";
import { Container } from "reactstrap";

const Footer = () => {
  return (
    <>
      <Container fluid className="p-0 py-5">
        <div className="footerback">
          <h4 className="f-size-60">Let's work together!</h4>
          <br />
          <div className="sp-btn-secondary rounded-0 px-5 btn btn-secondary">
            <span className="px-5">MAKE AN ENQUIRY</span>
          </div>
        </div>
        <div className="socialmedia-container">
          <div className="socialmedia-content px-3">
            <div className="socialmedia-icons fb"></div>
            <div className="socialmedia-icons tt"></div>
            <div className="socialmedia-icons wt"></div>
            <div className="socialmedia-icons li"></div>
            <div className="socialmedia-icons in"></div>
          </div>
        </div>
      </Container>
      <div className="p-color f-size-15 px-5 py-2 s-back">
        All rights reserved &copy; 2023
      </div>
    </>
  );
};

export default Footer;
