import React, { Fragment } from "react";
import NavBarComponent from "./shared-components/NavBarComponent";
import Footer from "./shared-components/Footer";
import { useSelector } from "react-redux";
import { ErrorAlert, SuccessAlert } from "./shared-components/AlertBox";
import { LoaderLG } from "./shared-components/loader";
const Base = (props) => {
  const { error, success, loading } = useSelector((state) => state.common);
  return (
    <>
      <NavBarComponent color="dark" expand="sm" sticky="top" dark />
      {props.children}
      {props.isFooter !== "hideFooter" && <Footer />}
      {error.length > 0 && (
        <div className="alert-wrapper">
          {error.map((item, i) => (
            <Fragment key={i}>
              <ErrorAlert message={item} />
            </Fragment>
          ))}
        </div>
      )}
      {success.length > 0 && (
        <div className="alert-wrapper">
          {success.map((item, i) => (
            <Fragment key={i}>
              <SuccessAlert message={item} />
            </Fragment>
          ))}
        </div>
      )}
      {loading && <LoaderLG pe={"pe-none"} />}
    </>
  );
};

export default Base;
